.store {
  min-height: 270px;
  background-color: @darkblue;
  color: @white;

  @media (min-width: @tablet-width) {
    .container();
    position: relative;
    z-index: 10;
    margin-top: -540px;
    margin-bottom: 109px;
    background-color: transparent;
  }

  @media (min-width: @desktop-width) {
    margin-top: -650px;
    margin-bottom: 121px;
  }
}

.store__content {
  .container();
  padding-top: 28px;

  @media (min-width: @tablet-width) {
    margin-left: 20px;
    width: 300px;
    padding-top: 15px;
  }

  @media (min-width: @desktop-width) {
    padding-top: 13px;
    margin-left: 10px;
  }
}

.store__title {
  display: none;
  padding: 0 30px;
  font-weight: 300;
  line-height: 1.2;
  text-align: center;


  @media (min-width: @tablet-width) {
    display: block;
    margin-top: 0;
    margin-bottom: 31px;
    font-size: 40px;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 52px;
    font-size: 50px;
  }

  &--first {
    width: 733px;
  }

  &--second {
    width: 727px;
  }
}

.store__list {
  .list-style();

  display: flex;
  justify-content: center;

  margin-top: 32px;
  margin-bottom: 21px;

  @media (min-width: @tablet-width) {
    margin-top: 45px;
    margin-bottom: 37px;
  }

  @media (min-width: @desktop-width) {
    margin-top: 67px;
    margin-bottom: 54px;
  }
}

.store__item {
  margin-left: 12px;
  margin-right: 9px;
}

.store__link {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  width: 56px;
  height: 58px;

  &--apple {
    background-image: url("../img/logo-apple.svg");
    background-repeat: no-repeat;
    background-position: 0 0;

    @media (min-width: @tablet-width) {
      background-position: -1px 2px;
    }

    @media (min-width: @desktop-width) {
      background-position: 0 1px;
    }
  }

  &--android {
    background-image: url("../img/logo-android.svg");
    background-repeat: no-repeat;
    background-position: 5px 8px;

    @media (min-width: @desktop-width) {
      background-position: 5px 9px;
    }
  }

  &--microsoft {
    background-image: url("../img/logo-microsoft.svg");
    background-repeat: no-repeat;
    background-position: 6px 14px;

    @media (min-width: @desktop-width) {
      background-position: 7px 12px;
    }
  }
}

.store__button {
  color: @white;
  border-color: @white;

  @media (min-width: @desktop-width) {
    font-size: 21px;
    line-height: 18px;
  }

  &:hover {
    color: @pink;
    background-color: @white;
    border-color: @white;
  }

  &:active {
    color: fade(@pink, 30%);
  }
}

.store__description {
  .list-style();

  font-weight: 300;
  text-align: center;

  @media (min-width: @tablet-width) {
    font-size: 18px;
  }
}
